export var streetlights="asyncapi: '2.6.0'\nid: 'urn:com:smartylighting:streetlights:server'\ninfo:\n  title: Streetlights API\n  version: '1.0.0'\n  description: |\n    The Smartylighting Streetlights API allows you to remotely manage the city lights.\n\n    ### Check out its awesome features:\n\n    * Turn a specific streetlight on/off \uD83C\uDF03\n    * Dim a specific streetlight \uD83D\uDE0E\n    * Receive real-time information about environmental lighting conditions \uD83D\uDCC8\n\n  termsOfService: http://asyncapi.org/terms/\n  contact:\n    name: API Support\n    url: http://www.asyncapi.org/support\n    email: support@asyncapi.org\n  license:\n    name: Apache 2.0\n    url: http://www.apache.org/licenses/LICENSE-2.0.html\ntags:\n  - name: root-tag1\n    externalDocs:\n      description: External docs description 1\n      url: https://www.asyncapi.com/\n  - name: root-tag2\n    description: Description 2\n    externalDocs:\n      url: \"https://www.asyncapi.com/\"\n  - name: root-tag3\n  - name: root-tag4\n    description: Description 4\n  - name: root-tag5\n    externalDocs:\n      url: \"https://www.asyncapi.com/\"\nexternalDocs:\n  description: Find more info here\n  url: https://example.com\ndefaultContentType: application/json\n\nservers:\n  production:\n    url: api.streetlights.smartylighting.com:{port}\n    protocol: mqtt\n    description: |\n      Private server that requires authorization.\n      Once the socket is open you can subscribe to private-data channels by sending an authenticated subscribe request message.\n\n      The API client must request an authentication \"token\" via the following REST API endpoint \"GetWebSocketsToken\" to connect to WebSockets Private endpoints. For more details read https://support.kraken.com/hc/en-us/articles/360034437672-How-to-retrieve-a-WebSocket-authentication-token-Example-code-in-Python-3\n\n      The resulting token must be provided in the \"token\" field of any new private WebSocket feed subscription: \n      ```json\n      {\n        \"event\": \"subscribe\",\n        \"subscription\":\n        {\n          \"name\": \"ownTrades\",\n          \"token\": \"WW91ciBhdXRoZW50aWNhdGlvbiB0b2tlbiBnb2VzIGhlcmUu\"\n        }\n      }\n      ```\n\n      ```elixir\n      defmodule Hello do\n        def world do\n          IO.puts(\"hello\")\n        end\n      end\n      ```\n    variables:\n      port:\n        description: Secure connection (TLS) is available through port 8883.\n        default: '1883'\n        enum:\n          - '1883'\n          - '8883'\n    tags:\n      - name: 'env:production'\n    security:\n      - apiKey: []\n      - supportedOauthFlows:\n        - streetlights:on\n        - streetlights:off\n        - streetlights:dim\n      - openIdConnectWellKnown: []\n  dummy-mqtt:\n    url: mqtt://localhost\n    protocol: mqtt\n    description: |\n      Private server\n\n      ```csharp\n      using System;\n\n      namespace HelloWorld\n      {\n        class Program\n        {\n          static void Main(string[] args)\n          {\n            Console.WriteLine(\"Hello World!\");    \n          }\n        }\n      }\n      ```\n    bindings:\n      mqtt:\n        clientId: guest        \n        cleanSession: false\n        keepAlive: 60\n        bindingVersion: 0.1.0\n        lastWill:\n          topic: smartylighting/streetlights/1/0/lastwill\n          qos: 1\n          message: so long and thanks for all the fish\n          retain: false\n  dummy-amqp:\n    url: amqp://localhost:{port}\n    protocol: amqp\n    description: dummy AMQP broker\n    protocolVersion: \"0.9.1\"\n    variables:\n      port:\n        enum:\n          - '15672'\n          - '5672'\n  dommy-kafka:\n    url: http://localhost:{port}\n    protocol: kafka\n    description: dummy Kafka broker\n    variables:\n      port:\n        default: '9092'\n\nchannels:\n  smartylighting/streetlights/1/0/event/{streetlightId}/lighting/measured:\n    x-security:\n      $ref: '#/components/securitySchemes/supportedOauthFlows/flows/clientCredentials'\n    description: The topic on which measured values may be produced and consumed.\n    parameters:\n      streetlightId:\n        $ref: '#/components/parameters/streetlightId'\n    servers:\n      - production\n      - dommy-kafka\n    subscribe:\n      summary: Receive information about environmental lighting conditions of a particular streetlight.\n      operationId: receiveLightMeasurement\n      externalDocs:\n        description: Find more info here\n        url: https://example.com\n      traits:\n        - $ref: '#/components/operationTraits/kafka'\n      message:\n        $ref: '#/components/messages/lightMeasured'\n      bindings:\n        mqtt:\n          qos: 1\n          bindingVersion: 0.1.0\n        http:\n          type: request\n          method: GET\n          query:\n            type: object\n            required:\n            - companyId\n            properties:\n              companyId:\n                type: number\n                minimum: 1\n                description: The Id of the company.\n            additionalProperties: false\n\n  smartylighting/streetlights/1/0/action/{streetlightId}/turn/on:\n    parameters:\n      streetlightId:\n        $ref: '#/components/parameters/streetlightId'\n    servers:\n      - production\n      - dummy-amqp\n    publish:\n      operationId: turnOn\n      security:\n        - supportedOauthFlows:\n          - streetlights:on\n      externalDocs:\n        description: Find more info here\n        url: https://example.com\n      traits:\n        - $ref: '#/components/operationTraits/kafka'\n      message:\n        $ref: '#/components/messages/turnOnOff'\n\n  smartylighting/streetlights/1/0/action/{streetlightId}/turn/off:\n    parameters:\n      streetlightId:\n        $ref: '#/components/parameters/streetlightId'\n    publish:\n      traits:\n        - $ref: '#/components/operationTraits/kafka'\n      message:\n        $ref: '#/components/messages/turnOnOff'\n\n  smartylighting/streetlights/1/0/action/{streetlightId}/dim:\n    parameters:\n      streetlightId:\n        $ref: '#/components/parameters/streetlightId'\n    servers:\n      - production\n      - dummy-amqp\n    publish:\n      operationId: dimLight\n      traits:\n        - $ref: '#/components/operationTraits/kafka'\n      message:\n        $ref: '#/components/messages/dimLight'\n\ncomponents:\n  messages:\n    lightMeasured:\n      messageId: lightMeasured Message ID\n      name: lightMeasured\n      title: Light measured\n      summary: Inform about environmental lighting conditions for a particular streetlight.\n      contentType: application/json\n      correlationId:\n        $ref: \"#/components/correlationIds/sentAtCorrelator\"\n      externalDocs:\n        url: \"https://www.asyncapi.com/\"\n      traits:\n        - $ref: '#/components/messageTraits/commonHeaders'\n      payload:\n        $ref: \"#/components/schemas/lightMeasuredPayload\"\n      bindings:\n        mqtt:\n          bindingVersion: 0.1.0\n      examples:\n        - headers:\n            my-app-header: 12\n          payload:\n            lumens: 1\n            sentAt: \"2020-01-31T13:24:53Z\"\n        - headers:\n            my-app-header: 13\n        - payload:\n            lumens: 3\n            sentAt: \"2020-10-31T13:24:53Z\"\n      x-schema-extensions-as-object:\n        type: object\n        properties:\n          prop1:\n            type: string\n          prop2:\n            type: integer\n            minimum: 0\n      x-schema-extensions-as-primitive: dummy\n      x-schema-extensions-as-array: \n        - \"item1\"\n        - \"item2\"\n    LwM2mOjbects:\n      payload:\n        type: object\n        properties:\n          objectLinks:\n            type: string\n        example:\n          objectLinks: \"lwm2m=1.1, </0/0>, </1/1>;ssid=1, </2>, </3/0>\"\n    turnOnOff:\n      name: turnOnOff\n      title: Turn on/off\n      summary: Command a particular streetlight to turn the lights on or off.\n      payload:\n        $ref: \"#/components/schemas/turnOnOffPayload\"\n      headers: \n        type: object\n        properties:\n          $ref: '#/components/schemas/streamHeaders'\n    dimLight:\n      name: dimLight\n      title: Dim light\n      summary: Command a particular streetlight to dim the lights.\n      correlationId:\n        $ref: \"#/components/correlationIds/sentAtCorrelator\"\n      externalDocs:\n        url: \"https://www.asyncapi.com/\"\n      tags:\n        - name: oparation-tag1\n          externalDocs:\n            description: External docs description 1\n            url: https://www.asyncapi.com/\n        - name: oparation-tag2\n          description: Description 2\n          externalDocs:\n            url: \"https://www.asyncapi.com/\"\n        - name: oparation-tag3\n        - name: oparation-tag4\n          description: Description 4\n        - name: oparation-tag5\n          externalDocs:\n            url: \"https://www.asyncapi.com/\"\n      traits:\n        - $ref: '#/components/messageTraits/commonHeaders'\n      payload:\n        $ref: \"#/components/schemas/dimLightPayload\"\n\n  schemas:\n    lightMeasuredPayload:\n      type: object\n      properties:\n        lumens:\n          type: integer\n          description: Light intensity measured in lumens.\n          writeOnly: true\n          oneOf: \n            - minimum: 0\n              maximum: 5\n            - minimum: 10\n              maximum: 20\n          externalDocs:\n            url: \"https://www.asyncapi.com/\"\n        sentAt:\n          $ref: \"#/components/schemas/sentAt\"\n        ifElseThen:\n          type: integer\n          minimum: 1\n          maximum: 1000\n          if:\n            minimum: 100\n          then: \n            multipleOf: 100\n          else:\n            if: \n              minimum: 10\n            then: \n              multipleOf: 10\n        dependencies:\n          $ref: \"#/components/schemas/dependenciesObject\"\n        anySchema: true\n        cannotBeDefined: false\n        restrictedAny: \n          minimum: 1\n          maximum: 1000\n      required:\n        - lumens\n      x-schema-extensions-as-object:\n        type: object\n        properties:\n          prop1:\n            type: string\n          prop2:\n            type: integer\n            minimum: 0\n      x-schema-extensions-as-primitive: dummy\n      x-schema-extensions-as-array: \n        - \"item1\"\n        - \"item2\"\n    turnOnOffPayload:\n      type: object\n      properties:\n        command:\n          type: string\n          enum:\n            - on\n            - off\n          description: Whether to turn on or off the light.\n        sentAt:\n          $ref: \"#/components/schemas/sentAt\"\n        arrayRank:\n          $ref: '#/components/schemas/arrayRank'\n      additionalProperties:\n        type: string\n\n    dimLightPayload:\n      type: object\n      properties:\n        percentage:\n          type: integer\n          description: Percentage to which the light should be dimmed to.\n          minimum: 0\n          maximum: 100\n          readOnly: true\n        sentAt:\n          $ref: \"#/components/schemas/sentAt\"\n        key:\n          type: integer\n          not:\n            minimum: 3\n      patternProperties:\n        ^S_:\n          type: string\n        ^I_:\n          type: integer\n      additionalProperties: false\n    sentAt:\n      type: string\n      format: date-time\n      description: Date and time when the message was sent.\n    union:\n      type: [string, number]\n    objectWithKey:\n      title: objectWithKey\n      type: object\n      propertyNames:\n        format: email\n      properties:\n        key:\n          type: string\n    objectWithKey2:\n      type: object\n      properties:\n        key2:\n          type: string\n          format: time\n    oneOfSchema:\n      oneOf:\n        - $ref: \"#/components/schemas/objectWithKey\"\n        - $ref: \"#/components/schemas/objectWithKey2\"\n    anyOfSchema:\n      anyOf:\n        - $ref: \"#/components/schemas/objectWithKey\"\n        - $ref: \"#/components/schemas/objectWithKey2\"\n    allOfSchema:\n      allOf:\n        - $ref: \"#/components/schemas/objectWithKey\"\n        - $ref: \"#/components/schemas/objectWithKey2\"\n    arrayContains: \n      type: array\n      contains:\n        type: integer\n    dependenciesObject:\n      type: object\n      properties:\n        name:\n          type: string\n        credit_card:\n          type: integer\n        billing_address:\n          type: string\n        schema_dependency:\n          type: string\n      required:\n        - name\n      dependencies:\n        credit_card:\n          properties:\n            billing_address:\n              type: string\n            billing_address2:\n              type: string\n          required:\n          - billing_address\n          dependencies:\n            billing_address2:\n              properties:\n                billing_address3:\n                  type: string\n              required:\n              - billing_address3    \n\n    subscriptionStatus:\n      type: object\n      oneOf:\n        - properties:\n            channelID:\n              type: integer\n              description: ChannelID on successful subscription, applicable to public messages only.\n            channelName:\n              type: string\n              description: Channel Name on successful subscription. For payloads 'ohlc' and 'book', respective interval or depth will be added as suffix.\n        - properties:\n            errorMessage:\n              type: string\n      properties:\n        event:\n          type: string\n          const: subscriptionStatus\n        subscription:\n          type: object\n          properties:\n            depth:\n              type: string\n            interval:\n              type: string\n          required:\n            - name\n      required:\n        - event\n\n    arrayRank:\n      type: object\n      properties:\n        valueRank: \n          $ref: '#/components/schemas/arrayValueRank'\n        arrayDimensions: \n          $ref: '#/components/schemas/arrayArrayDimensions'\n\n    arrayValueRank:\n      description: >\n        This Attribute indicates whether the val Attribute of the datapoint is an\n        array and how many dimensions the array has.\n      type: integer\n      default: -1\n      examples:\n        - 2\n      oneOf:\n        - const: -1\n          description: 'Scalar: The value is not an array.'\n        - const: 0\n          description: 'OneOrMoreDimensions: The value is an array with one or more dimensions.'\n        - const: 1\n          description: 'OneDimension: The value is an array with one dimension.'\n        - const: 2\n          description: 'The value is an array with two dimensions.'\n\n    arrayArrayDimensions:\n      type: array\n      items:\n        type: integer\n        minimum: 0\n      examples:\n        - [3, 5]\n\n    streamHeaders:\n      Etag:\n        type: string\n        description: |\n          The RFC7232 ETag header field in a response provides the current entity-\n          tag for the selected resource. An entity-tag is an opaque identifier for\n          different versions of a resource over time, regardless whether multiple\n          versions are valid at the same time. An entity-tag consists of an opaque\n          quoted string, possibly prefixed by a weakness indicator.\n        example: 411a\n      Cache-Control:\n        description: The Cache-Control HTTP header holds directives (instructions) for caching in request.\n        type: string\n        example: no-cache, no-store, must-revalidate\n\n  securitySchemes:\n    apiKey:\n      type: apiKey\n      in: user\n      description: Provide your API key as the user and leave the password empty.\n    supportedOauthFlows:\n      type: oauth2\n      description: Flows to support OAuth 2.0\n      flows:\n        implicit:\n          authorizationUrl: 'https://authserver.example/auth'\n          scopes:\n            'streetlights:on': Ability to switch lights on\n            'streetlights:off': Ability to switch lights off\n            'streetlights:dim': Ability to dim the lights\n        password:\n          tokenUrl: 'https://authserver.example/token'\n          scopes:\n            'streetlights:on': Ability to switch lights on\n            'streetlights:off': Ability to switch lights off\n            'streetlights:dim': Ability to dim the lights\n        clientCredentials:\n          tokenUrl: 'https://authserver.example/token'\n          scopes:\n            'streetlights:on': Ability to switch lights on\n            'streetlights:off': Ability to switch lights off\n            'streetlights:dim': Ability to dim the lights\n        authorizationCode:\n          authorizationUrl: 'https://authserver.example/auth'\n          tokenUrl: 'https://authserver.example/token'\n          refreshUrl: 'https://authserver.example/refresh'\n          scopes:\n            'streetlights:on': Ability to switch lights on\n            'streetlights:off': Ability to switch lights off\n            'streetlights:dim': Ability to dim the lights\n    openIdConnectWellKnown:\n      type: openIdConnect\n      openIdConnectUrl: 'https://authserver.example/.well-known'\n\n  parameters:\n    streetlightId:\n      description: The ID of the streetlight.\n      schema:\n        type: string\n      location: \"$message.payload#/user/id\"\n\n  correlationIds:\n    sentAtCorrelator:\n      description: Data from message payload used as correlation ID\n      location: $message.payload#/sentAt\n\n  messageTraits:\n    commonHeaders:\n      headers:\n        type: object\n        properties:\n          my-app-header:\n            type: integer\n            minimum: 0\n            maximum: 100\n        required:\n          - my-app-header\n  \n  operationTraits:\n    kafka:\n      bindings:\n        kafka:\n          clientId: my-app-id\n";